.App {
  text-align: center;
}

.App-logo {
  animation: 2s infinite beatHeart;
  padding-top: 20px;
  height: 20vmin;
}

.App-header {
  background-color: #60DAFF;
  /* background-color: #282c34; */
  /* min-height: 100vh; */
  min-height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 10px;
}

.App-medical-header {
  /* min-height: 100vh; */
  min-height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(40px + 2vmin);
  color: black;
  padding-bottom: 10px;
}

.App-link {
  color: #61dafb;
}

.App-div-inline {
  margin-bottom: 10px;
  display: inline-block;
}

.App-div {
  margin-bottom: 10px;
}

.App-input {
  height: 3vmin;
}

.App-label {
  margin-right: 1vmin;
  font-size: 3vmin;
}

.App-submit-button {
  height: 5vmin;
}

@keyframes App-logo-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

@keyframes beatHeart {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  40% {
    transform: scale(1);
  }
  60% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}